@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,600;1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Amarante&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
.FavoriteIcon {
    padding: 6px;
}

.FavoriteIcon:hover {
	cursor: pointer;
}

.shop-container-card {
	margin: 0px 120px;
}

.shop-container-card > aside {
	display: flex;
	flex-direction: row-reverse;
	padding: 10px;
}

.shop-container-card:hover {
	background-color: #f2f3f4;
	-webkit-transform: scale(1.02);
	        transform: scale(1.02);
}

.shop-container {
	/* 
	This will display it in a column like the blog section.
	display: flex;
	flex-direction: column;
	justify-content: center; */
	display: inline-block;
	width: 50%;
	margin-left: auto;
	margin-right: auto;
	margin-top: 3rem;
	margin-bottom: 1rem;
	text-align: center;
}

.shop-container:nth-child(5) {
	position: absolute;
}

.shop-hero {
	background-image: url('https://www.onlineexpo.com/site/assets/files/5437/space-1.1920x1080.jpg');
	height: 20vh;
	display: flex;
	justify-content: center;
	background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.shop-hero > h1 {
	margin-top: 3.5rem;
	color: #f2f3f4;
}

.shop-container > article {
	margin-top: 15px;
	margin-left: 20px;
	margin-right: 20px;
	text-align: center;
}

.shop-container > aside {
	display: flex;
	flex-direction: row-reverse;
	padding: 10px;
    margin-right: 10px;
}

.shop-container > footer, ul, li {
	margin: 7px 0px;
	list-style: none; 
}

.shop-greeting {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: 0 auto;
    width: 65%;
    text-align: center;
	color: #f2f3f4;
	font-family: 'Poppins';
	font-size: 28px;
	cursor: default;
}

.shop-greeting > h1 {
	background: linear-gradient(#333, #f2f3f4);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.shop-greeting > h4 {
	font-family: 'Poppins';
	font-size: 20px;
	-webkit-text-decoration: underline #fcc200;
	        text-decoration: underline #fcc200;
	color: #f2f3f4;
}

@media only screen and (max-width:  999px) {
	.shop-container {
		width: 100vw;
	}
	.shop-greeting {
		width: auto;
		font-size: 18px;
		padding: 0px 50px;
	}
	.shop-container-card {
		width: auto;
		text-align: left;
		padding: 0px;
	}
	.shop-container-card > article {
		width: auto;
		padding-top: 5px;
		padding-bottom: 5px;
		padding-left: 50px;
		padding-right: 20px;
	}
	article, footer {
		padding: auto;
		display: flex;
		justify-content: center;
	}
}
.primary-nav {
    display: flex;
    justify-content: space-around;
    /* justify-content: flex-end; */
    background: linear-gradient(to right, transparent,white, white, transparent);
    padding: 8px;
    font-size: 17px;
    font-weight: bold;
}

.nav-link {
    color: black;
    text-decoration: none;
    cursor: pointer;
    padding: 5px;
}

.nav-link:hover {
    background: linear-gradient(#333, #0f0f0f);
    color: #f2f3f4;
    border-radius: 10px;
}
.FavoriteIcon {
	color: #FF1493;
}
.blogpost-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	width: 65%;
	margin-left: auto;
	margin-right: auto;
	margin-top: 3rem;
	margin-bottom: 1rem;
	text-align: center;
}

.blogpost-hero {
	/* background-image: url('https://artbyamyp.com/wp-content/uploads/2017/06/IMG_0871-767x1024.jpg'); */
	background-image: url('https://images.fineartamerica.com/images/artworkimages/mediumlarge/2/black-and-white-contemporary-abstract-circle-painting-holly-anderson.jpg');
	height: 20vh;
	background-size: cover;
}

.blogpost-hero > h1 {
	margin-top: 3.5rem;
}

.blogpost-container > article {
	margin-top: 5rem;
	text-indent: 3rem;
	line-height: 2.5rem;
	/* margin-left: 20px;
	margin-right: 20px; */
	margin-left: auto;
	margin-right: auto;
	width: 55%;
	text-align: left;
}

.blogpost-container > aside {
	display: flex;
	flex-direction: row-reverse;
	padding: 10px;
}

.blogpost-container > footer, ul, li {
	margin: 7px 0px;
	list-style: none;
}

.comment-container {
	margin-top: 2rem;
}

.comment-container > section {
	border: 1px solid gray;
	background-color: ghostwhite;
}

.comment-container > section, p {
	width: 500px;
	height: 200px;
	margin: 0 auto;
	padding: 10px 5px;
	text-align: left;
	font-size: 14px;
	font-weight: light;
	display: flex;
	flex-direction: column-reverse;
	color: black;
}

.comment-container > textarea {
	background-color: ghostwhite;
	color: black;
}

// below can be deleted if not used for new layout.

Button.add-blogpost-btn {
	padding: .7rem;
	width: 24%;
	margin-left: auto;
	margin-right: auto;
}

.add-blogpost-link {
	text-decoration: none;
	color: white;
	font-weight: bold;
}
.Auth-form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 80vh;
}

.Auth-form {
    width: 490px;
    box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
    padding-top: 25px;
    padding-bottom: 20px;
    border-radius: 8px;
    background: linear-gradient(to bottom, transparent, white, white, transparent);
}

.Auth-form-content {
    padding-left: 12%;
    padding-right: 12%;
}

.Auth-form-title {
    text-align: center;
    margin-bottom: 1em;
    font-size: 24px;
    font-weight: 800;
}

label {
    font-size: 14px;
    font-weight: 600;
}

.sign-up {
    cursor: pointer;
    font-weight: bold;
    color: black;
}

.not-registered {
    text-align: center;
}

.form-group {
    margin-top: 0.75rem;
}

.form-control {
    margin-top: 0.25rem;
}

Button.btn {
    margin-top: 1rem;
    padding: 3px;
    width: 7rem;
}

.already-registered {
    text-align: center;
    margin-top: 10px;
}

.submit-grid {
    display: grid;
    grid-gap: 2;
    gap: 2;
}

.user-list-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #f2f3f4;
}

.add-user-link {
    color: #f2f3f4;
    text-decoration: none;
}

.user-list-container > h3 {
    font-size: 32px;
}

th {
    padding: 0 2rem;
}

td {
    border: 1px solid white;
    text-align: center;
    padding: 5px;
}

tr:hover {
	background-color: #1f1f1f;
}

.table-heading:hover {
	background-color: #0f0f0f;
}

tr > td > Button:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}
.update-users {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #0f0f0f;
    background: linear-gradient(to bottom, transparent, white, white, white, transparent);
    width: 40%;
    margin: auto;
    padding: 0px;
    margin-top: 5rem;
}

.form-group-btn {
    padding: 2rem 0px;
}
html {
    background-color: #0f0f0f;
}

body {
    font-family: 'Open Sans', 'Amarante', 'Poppins', sans-serif;
    font-weight: bold;
    color: black;
    padding: 0;
    margin: 0;
}

::-webkit-scrollbar {
    background: #0f0f0f;
    width: 4px;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    background: #f2f3f4;
    border-radius: 10px;
}
